<template>
  <div ref="ex">
    <div class="correct">
      <div class="font-bold text-center text-3xl pb-10">Correct Examples</div>
      <div
        class="examplesPage w-8/12 text-center rounded-3xl"
        style="margin: 0px auto; height: 700px"
      >
        <div class="carousel-container">
          <div
            class="carousel-slide"
            v-for="(image, index) in images"
            :key="index"
            v-show="index === activeIndex"
          >
            <img class="w-full h-full" :src="image" :alt="'Image ' + index" />
          </div>
          <div
            class="cursor-pointer absolute bottom-1/2 -left-32 rounded-full w-11 h-11 shadow-md flex justify-center items-center hover:scale-125 transition-all"
            @click="prev"
          >
            <img src="resources/img/figures/left.png" class="scale-50" alt="" />
          </div>
          <div
            class="w-5/6 h-8 flex justify-center items-center absolute bottom-6"
          >
            <div
              v-for="(item, index) in images"
              :key="item"
              class="rounded-full w-3 h-3 shadow-md shadow-black ml-1 cursor-pointer hover:scale-110 transition-all"
              @click="changeExamples(index)"
            ></div>
          </div>

          <div
            class="cursor-pointer absolute bottom-1/2 -right-32 rounded-full w-11 h-11 shadow-md flex justify-center items-center hover:scale-125 transition-all"
            @click="next"
          >
            <img
              src="resources/img/figures/right.png"
              class="scale-50"
              alt=""
            />
          </div>
        </div>
      </div>
    </div>
    <div class="error">
      <div class="font-bold text-center text-3xl pb-10 pt-20">
        Error Examples
      </div>
      <div
        class="examplesPage w-8/12 text-center rounded-3xl"
        style="margin: 0px auto; height: 700px"
      >
        <div class="carousel-container">
          <div
            class="carousel-slide"
            v-for="(image, index) in images"
            :key="index"
            v-show="index === activeErrIndex"
          >
            <img class="w-full h-full" :src="image" :alt="'Image ' + index" />
          </div>
          <div
            class="cursor-pointer absolute bottom-1/2 -left-32 rounded-full w-11 h-11 shadow-md flex justify-center items-center hover:scale-125 transition-all"
            @click="prevErr"
          >
            <img src="resources/img/figures/left.png" class="scale-50" alt="" />
          </div>
          <div
            class="w-5/6 h-8 flex justify-center items-center absolute bottom-6"
          >
            <div
              v-for="(item, index) in images"
              :key="item"
              class="rounded-full w-3 h-3 shadow-md shadow-black ml-1 cursor-pointer hover:scale-110 transition-all"
              @click="changeErrExam(index)"
            ></div>
          </div>

          <div
            class="cursor-pointer absolute bottom-1/2 -right-32 rounded-full w-11 h-11 shadow-md flex justify-center items-center hover:scale-125 transition-all"
            @click="nextErr"
          >
            <img
              src="resources/img/figures/right.png"
              class="scale-50"
              alt=""
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: [
        "./resources/img/figures/attribute_half.png",
        "./resources/img/figures/corruption_illustration.png",
        "./resources/img/figures/dataset_illustration.png",
        // 添加更多图片路径
      ],
      activeIndex: 0, // 当前显示的图片索引
      activeErrIndex: 0,
    };
  },
  methods: {
    next() {
      this.activeIndex++;
      if (this.activeIndex >= this.images.length) {
        this.activeIndex = 0;
      }
    },
    prev() {
      this.activeIndex--;
      if (this.activeIndex < 0) {
        this.activeIndex = this.images.length - 1;
      }
    },
    nextErr() {
      this.activeErrIndex++;
      if (this.activeErrIndex >= this.images.length) {
        this.activeErrIndex = 0;
      }
    },
    prevErr() {
      this.activeErrIndex--;
      if (this.activeErrIndex < 0) {
        this.activeErrIndex = this.images.length - 1;
      }
    },
    changeExamples(idx) {
      this.activeIndex = idx;
    },
    changeErrExam(idx) {
      this.activeErrIndex = idx;
    },
  },
};
</script>

<style scoped>
.examplesPage {
  box-shadow: 3px 3px 3px 3px rgb(213, 211, 211);
}
.carousel-container {
  position: relative;
  width: 70%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.carousel-slide {
  position: absolute;
  transition: 0.5s ease;
}
</style>
