        >
        <br />
        Given the often low relevance between images and their captions, we
        prompt GPT-4V to focus on describing the morphology of cells and
        tissues, and by providing original captions as references, we
        significantly reduce incoherent content, ensuring the generation of more
        precise and relevant descriptions.
        <br />
        <strong>Step 3: Question Generation and Expert Validation.</strong
        ><br />
        After generating descriptions for each image, GPT-4V is tasked with
        creating three interpretive questions per image, complete with
        multiple-choice options, correct answers, and explanations.
        <br /><br />
        To counteract the model's tendency to bypass image analysis, we utilized
        ChatGPT, GPT-4, Gemini Pro, and Ernie-4 for educated guessing, excluding
        any question from the dataset if at least three models guessed
        correctly.
